.podrobno {
	background-color: $blue_middle;
	&__image {
		width: 100%;
		height: 0;
		padding-bottom: 30%;
		position: relative;
		background-color: $color_main_bg;
		background-size: cover;
		background-repeat: no-repeat; }
	&__gradient {
		position: absolute;
		top: 0;
		width: 100%;
		height: 99%;
		background: linear-gradient(to bottom, rgba(255, 255, 255, 0.9) 0%, rgba(0, 0, 0, 0.1) 100%); }
	&__title {
		display: flex;
		flex-direction: column;
		padding: 15px 115px;
		resize: both;
		position: relative;
		z-index: 2; }
	&__main {
		margin-top: -4px;
		background-color: $color_white_blue; }
	&__drop {
		position: relative;
		top: -100px; }
	&__content {
		padding: 0 115px 65px;
		height: 265px;
		overflow: hidden;
		transition: height 0.3s; }
	&__text {
		font-size: 14px;
		line-height: 22px;
		color: $blue_light;
		border-top: 3px solid $orange;
		background-color: $blue_middle;
		padding: 0 115px 65px;
		h2 {
			margin: 45px 0;
			text-align: center; }
		p {
			padding-bottom: 60px; } }
	.open {
		transform: rotate(0deg); }
	&__text-more {
		position: absolute;
		bottom: -30px;
		left: 50%;
		margin-left: -30px;
		background: none;
		width: 60px;
		height: 60px;
		border-radius: 50%;
		background-color: $orange;
		transition: background 0.3s;
		transform: rotate(180deg);
		&:hover {
			background-color: $blue; } }
	&__box-slider {
		position: relative;
		padding: 0 115px 80px;
		&.is-open {

			.slider-prevArrow,
			.slider-nextArrow {
				display: none; } }
		.slick-list {
			height: 350px; }
		.slider-prevArrow {
			top: 170px; }
		.slider-nextArrow {
			top: 170px; } }
	&__slider {
		// height: 350px
		text-align: center;
		.slide {
			vertical-align: top;
			padding: 35px 10px 0;
			max-width: 250px;
			max-height: 320px;
			a {
				font-weight: 500;
				margin-top: 35px;
				font-size: 18px;
				color: $blue;
				transition: color 0.3s; }
			&:hover {
				a {
					color: $orange; } }
			&-image {
				margin: 0 auto 25px;
				max-width: 235px;
				max-height: 235px;
				cursor: pointer;
				img {
					width: 100%; } } } }
	&__btn-lodmore {
		display: block;
		max-width: 240px;
		background: none;
		margin: 35px auto 0;
		padding: 4px 15px;
		border: 1px solid $blue;
		border-radius: 15px;
		color: $blue;
		font-size: 14px;
		cursor: pointer;
		transition: color 0.3s, border 0.3s;
		&:hover {
			border: 1px solid $orange;
			color: $orange; } }
	&__brends {
		padding: 0 115px 65px; }
	&__popup {
		display: none;
		position: relative;
		width: 420px;
		height: 395px;
		border-radius: 10px;
		background: $blue_middle;
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		opacity: 0;
		z-index: 8;
		padding: 20px 10px;
		span {
			padding-left: 25px;
			font-size: 12px;
			color: $orange; } }
	&__popup-text {
		overflow: auto;
		max-height: 100px;
		padding: 10px 25px 25px;
		font-size: 12px;
		color: $blue_light;
		line-height: 22px;
		&::-webkit-scrollbar {
			width: 6px;
			background-color: #F5F5F5;
			border-radius: 3px; }
		&::-webkit-scrollbar-thumb {
			background-color: #d0dbe2;
			border-radius: 3px; } }
	&__popup-close {
		position: absolute;
		top: 15px;
		right: 15px; }
	.icon-close {
		fill: $blue;
		cursor: pointer; }
	&__popup-brend {
		position: relative;
		width: 230px;
		height: 70px;
		margin: 10px auto 5px;
		.icon-sberbank_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 43px; }
		.icon-huawei_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 75px; }
		.icon-gazprom_logo {
			position: absolute;
			top: 50%;
