			transform: translate(-50%, -50%);
			font-size: 75px; }
		.icon-alfabank_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 46px; }
		.icon-google_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 35px; }
		.icon-skb-bank_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 39px; }
		.icon-apple_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 74px; }
		.icon-mts_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 46px; }
		.icon-intel_logo {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 75px; } }
	&__title-brend {
		text-align: center;
		margin-bottom: 40px; }
	&__popup-overlay {
		z-index: 5;
		position: fixed;
		background-color: rgba(46, 91, 114, 0.5);
		opacity: 0.8;
		-moz-opacity: 0.8;
		filter: alpha(opacity=80);
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		cursor: pointer;
		display: none; }
	&__brends-container {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		.brend {
			margin: 30px 0 0;
			position: relative;
			width: 230px;
			height: 100px;
			.icon-sberbank_logo {
