				transform: translate(-50%, -50%);
				font-size: 39px; }
			.icon-apple_logo {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: 74px; }
			.icon-mts_logo {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: 46px; }
			.icon-intel_logo {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: 75px; } } }
	&__application {
		background-color: $blue_middle;
		border-top: 3px solid $orange; }
	&__application-position {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
		padding: 0 115px; }
	&__left-block {
		flex: 3 1 45%;
		margin: 45px 75px 45px 0;
		min-width: 400px; }
	&__row-input {
		margin-top: 30px;
		width: 100%;
		input {
			font-size: 14px;
			height: 47px;
			width: 100%;
			padding: 0 20px;
			background: #cbd9e1;
			border-radius: 25px;
			color: $blue_light; }
		input::-webkit-input-placeholder {
			color: $blue_light; }
		input:focus::-webkit-input-placeholder {
			opacity: 0;
			transition: {}
			opacity 0.3s ease {} } }
	&__row-select {
		margin-top: 30px; }
	&__row-textarea {
		margin-top: 30px;
		textarea {
			font-size: 14px;
			border: none;
			height: 94px;
			width: 100%;
			padding: 10px 20px;
			background: #cbd9e1;
			border-radius: 25px;
			color: $blue_light;
			resize: none;
			overflow: auto; }
		textarea::-webkit-input-placeholder {
			color: $blue_light; }
		textarea:focus::-webkit-input-placeholder {
			opacity: 0;
			transition: {}
			opacity 0.3s ease {} } }
	&__form-button {
		background: none;
		margin-top: 30px;
		padding: 12px 35px;
		border-radius: 20px;
		background-color: $orange;
		color: #fff;
		font-size: 14px;
		transition: background 0.3s;
		&:hover {
			background-color: $blue; } }
	&__right-block {
		flex: 1 1 45%;
		margin: 75px 0 45px;
		min-width: 400px;
		h2 {
			margin-bottom: 35px; }
		p {
			font-size: 14px;
			line-height: 22px;
			color: $blue_light; } } }
.select2-dropdown {
	border: none;
	border-radius: 0 0 20px 20px; }


.select2-container--default .select2-selection--single .select2-selection__rendered {
	color: $blue_light;
	font-size: 14px; }
.select2-container .select2-selection--single {
	height: 47px;
	border-radius: 25px;
	padding: 10px 10px;
	border: none; }
.select2-container--default .select2-selection--single {
	border: none;
	background-color: #cbd9e1; }
.select2-container--default .select2-selection--single .select2-selection__arrow {
	height: 47px;
	right: 15px;
	top: 50%; }
.select2-container--default .select2-selection--single .select2-selection__arrow b {
	border: none;
	display: block;
	content: '';
	background-image: url(../img/arrow-blue.png);
	width: 13px;
	height: 8px;
	position: absolute;
	top: 0; }
.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
	transform: rotate(180deg); }
.select2-results {
	border-radius: 0 0 20px 20px;
	background-color: #cbd9e1;
	font-family: $gotham500;
	color: $blue_light;
	font-size: 14px; }
.select2-results__option {}

.select2-results__options {
	border: none;
	padding: 10px; }
.select2-container--default .select2-results__option[aria-selected=true] {
	background-color: inherit; }
.select2-container--default .select2-results__option--highlighted[aria-selected] {
	background-color: inherit;
	color: $blue; }
.select2-selection__arrow b {
	border: none; }
