.news-btn {
	position: absolute;
	display: block;
	right: 15px;
	bottom: 10px;
	background: none;
	color: $blue;
	font-family: $gotham500;
	font-weight: 400;
	font-size: 14px;
	transition: background 0.3s, color 0.3s;
	&:hover {
		color: $orange; } }



.news {
	height: 100%;
	background-color: $color_main_bg;
	&__container {
		font-size: 16px;
		padding: 15px 85px 0; } }

.news__title {
	display: flex;
	flex-direction: column;
	position: relative;
	padding-bottom: 65px; }

.bread-crams {
	position: relative;
	.title-step {
		position: absolute;
		margin-bottom: 0;
		top: 30px;
		left: 0; } }



.news__menu__dropdown {
	position: relative;
	right: 0;
	bottom: 10px;
	.news__dropdown__box {
		position: absolute;
		right: 0;
		z-index: 1;
		.dropdown-toggle {
			font-family: $gotham500;
			font-size: 14px;
			font-weight: 600;
			color: $blue;
			background: none;
			.icon-arrow-down {
				width: 10px;
				height: 6px;
				color: $blue_light;
				padding-bottom: 2px; } }
		.news-directions {
			background-color: $color_white_blue;
			padding: 5px 10px;
			margin-top: 5px;
			display: none;
			z-index: 1;
			.categories {
				display: block;
				font-family: $gotham500;
				font-size: 13px;
				font-weight: 500;
				color: $blue;
				background: none;
				padding-bottom: 5px;
				transition: color 0.3s;
				&:hover {
					color: $orange; } } } } }

.text_review {
	color: $blue;
	font-size: 16px;
	font-weight: 600; }

