.button__loading {
	display: flex;
	justify-content: center;

	.loading__more {
		text-align: center;
		margin: 45px 0 40px;
		padding: 3px 15px;
		background: none;
		border: 1px solid $blue;
		border-radius: 15px;
		color: $blue;
		transition: background 0.3s, border 0.3s, color 0.3s;
		&:hover {
			border: 1px solid $blue;
			background: $blue;
			color: #fff; } } }
