  border: 1px solid #000;
  opacity: 0.1;
  z-index: 1;
}

.vnedrenie__top::before {
  position: absolute;
  content: '';
  top: -150px;
  left: -150px;
  border-radius: 50%;
  width: 450px;
  height: 450px;
  border: 1px solid #000;
  z-index: 1;
  opacity: 0.1;
}

.vnedrenie__bottom {
  flex: 1 1 50%;
  color: #396f8c;
  font-size: 14px;
  line-height: 22px;
  margin: 115px 0 25px;
  min-height: 300px;
}

.vnedrenie__bottom h2 {
  margin-bottom: 25px;
}

.vnedrenie__bottom p {
  margin-bottom: 15px;
}

.vnedrenie__bottom .back-btn {
  background: none;
  color: #ee9f32;
  font-size: 14px;
}

.vnedrenie-slider {
  position: relative;
  padding: 0 115px 40px;
  background-color: #dce4ea;
}

.vnedrenie-slider h2 {
  padding: 20px 0;
}

.vnedrenie-slider .slid-image {
  margin: 0 auto 25px;
  max-width: 200px;
  max-height: 200px;
  cursor: pointer;
}

.vnedrenie-slider .slid-image img {
  width: 100%;
}

.vnedrenie-slider .slid-link {
  font-size: 16px;
  color: #2e5b72;
  text-align: center;
  font-weight: 700;
  transition: color 0.3s;
}

.slider-prevArrow {
  position: absolute;
  width: 60px;
  height: 9px;
  top: 170px;
  left: 70px;
  cursor: pointer;
  z-index: 1;
}

.slider-prevArrow .icon-next-arrow {
  transform: rotate(180deg);
  font-size: 10px;
  fill: #fff;
}

.slider-nextArrow {
  position: absolute;
  width: 60px;
  height: 9px;
  top: 170px;
  right: 70px;
  cursor: pointer;
  z-index: 1;
}

.slider-nextArrow .icon-next-arrow {
  font-size: 10px;
  fill: #fff;
}

.slid {
  padding: 10px;
  width: 100%;
  text-align: center;
}

.slid:hover .slid-link {
  color: #ee9f32;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
