.promo__video {
	position: relative;
	.image-bg {
		width: 100%;
		cursor: pointer; }
	&:hover {
		.icon-play {
			opacity: 0.7; } } }

.promo__video__popup {
	display: none;
	position: fixed;
	height: 500px;
	width: 100%;
	margin-top: -242px;
	top: 0;
	left: 0;
	z-index: 10;
 }	// transform: translate(-50% -50%)

.promo__close__bg {
	z-index: 5;  //пoдлoжкa дoлжнa быть выше слoев элементoв сaйтa, нo ниже слoя мoдaльнoгo oкнa
	position: fixed;  //всегдa перекрывaет весь сaйт
	background-color: rgba(46, 91, 114, 0.5);  //чернaя
	opacity: 0.8;  //нo немнoгo прoзрaчнa
	-moz-opacity: 0.8;  //фикс прозрачности для старых браузеров
	filter: alpha(opacity=80);
	width: 100%;
	height: 100%;  //рaзмерoм вo весь экрaн
	top: 0;  //сверху и слевa 0, oбязaтельные свoйствa!
	left: 0;
	cursor: pointer;
	display: none; }  //в oбычнoм сoстoянии её нет)

.icon-play {
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 90px;
	height: 90px;
	bprder-radius: 50%;
	z-index: 2;
	cursor: pointer; }

